            <p>Teknisk leder fungerer som representant for det tekniske inn mot styret og admin gruppen. Jobben innebrer derfor ogs  jobbe sammen med de administrative stillingene. Oppgaver som  planlegge innkjp av teknisk utstyr sammen med konomiansvarlig og bist med formidlingen av det tekniske arbeidet sammen med kommunikasjonslederen.</p>
